import { Component } from 'react/cjs/react.development';
import MarvelService from '../../servises/MarvelService';
import Spinner from './../Spinner/Spinner';
import ErrorMessage from '../errorMessage/errorMesssage';
import './randomChar.scss';
import mjolnir from '../../resources/img/mjolnir.png';

class RandomChar extends Component {
	state = {
		char: {},
		loading: true,
		error: false,
		
	};
	onError = () => {
		this.setState({ loading: false, error: true });
	};
	marvelServise = new MarvelService();
	onCharLoaded = char => {
		this.setState({ char, loading: false });
	};
	updateChar = () => {
		const id = Math.floor(Math.random() * (1011400 - 1011000) + 1011000);
		this.marvelServise
			.getCharacter(id)
			.then(this.onCharLoaded)
			.catch(this.onError);
	};
	componentDidMount() {
		this.updateChar();
	}
	render() {
		const { char, loading, error } = this.state;
		const errorMessage = error ? <ErrorMessage /> : null;
		const spinner = loading ? <Spinner /> : null;
		const content = !(loading || error) ? <View char={char} /> : null;
		if (
			char.thumbnail ===
			'http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg'
		) {
		}
		return (
			<div className='randomchar'>
				{errorMessage}
				{spinner}
				{content}
				<div className='randomchar__static'>
					<p className='randomchar__title'>
						Random character for today!
						<br />
						Do you want to get to know him better?
					</p>
					<p className='randomchar__title'>Or choose another one</p>
					<button
						className='button button__main'
						onClick={this.updateChar}
					>
						<div className='inner'>try it</div>
					</button>
					<img
						src={mjolnir}
						alt='mjolnir'
						className='randomchar__decoration'
					/>
				</div>
			</div>
		);
	}
}

export default RandomChar;

const View = ({ char }) => {
	const { name, descpription, thumbnail, homepage, wiki } = char;
	let imgStyle = {};
	if (thumbnail ==='http://i.annihil.us/u/prod/marvel/i/mg/b/40/image_not_available.jpg') {
		imgStyle = { objectFit: 'contain' };
	}
	return (
		<div className='randomchar__block'>
			<img
				src={thumbnail}
				style={imgStyle}
				alt='Random character'
				className='randomchar__img'
			/>
			<div className='randomchar__info'>
				<p className='randomchar__name'>{name}</p>
				<p className='randomchar__descr'>{descpription}</p>
				<div className='randomchar__btns'>
					<a href={homepage} className='button button__main'>
						<div className='inner'>homepage</div>
					</a>
					<a href={wiki} className='button button__secondary'>
						<div className='inner'>Wiki</div>
					</a>
				</div>
			</div>
		</div>
	);
};
